<template lang="">
    <div class="">
        <ul class="tab-nav normal-box">
            <li v-for="(item,index) in tabNavs" @click="selectTab(index)"  :class="{active:tabNavs[index].active}">
                {{item.name}}
            </li>
        </ul>
       <!--  <router-view ></router-view> -->
        <mt-tab-container v-model="selected" style="height:100%;" :class="my-tab-container" >
            <mt-tab-container-item :id="tabNavs[0].selectedId">
                <mt-field label="证券账户" placeholder="请输入证券账户" type="trdAcct" v-model="trdAcct"></mt-field>
                <mt-field label="证券代码" placeholder="请输入证券代码" type="hqzqdm" v-model="hqzqdm"></mt-field>
                <mt-field  v-if=false label="证券名称:"  type="hqzqjc" v-model="hqzqjc"></mt-field>
                <mt-field label="价格" placeholder="请输入价格" type="orderPrice" v-model="orderPrice"></mt-field>
                <mt-field  v-if=false  label="可申报数量"   v-model="maxnum"></mt-field>
                <mt-field label="申报数量" placeholder="请输入申报数量" type="orderQty" v-model="orderQty"></mt-field>
                <mt-button type="danger" size="large" @click="payBuy">定价买入</mt-button>
            </mt-tab-container-item>
            <mt-tab-container-item :id="tabNavs[1].selectedId">
                <mt-field label="证券账户" placeholder="请输入证券账户" type="trdAcct" v-model="trdAcct"></mt-field>
                <mt-field label="证券代码" placeholder="请输入证券代码" type="hqzqdm" v-model="hqzqdm"></mt-field>
                <mt-field  v-if=false label="证券名称:"  type="hqzqjc" v-model="hqzqjc"></mt-field>
                <mt-field label="价格" placeholder="请输入价格" type="orderPrice" v-model="orderPrice"></mt-field>
                <mt-field  v-if=false  label="可申报数量"   v-model="maxnum"></mt-field>
                <mt-field label="申报数量" placeholder="请输入申报数量" type="orderQty" v-model="orderQty"></mt-field>
                <mt-button type="danger" size="large" @click="paySell">定价卖出</mt-button>
            </mt-tab-container-item>
            <mt-tab-container-item :id="tabNavs[2].selectedId">
                <mt-field label="证券账户" placeholder="请输入证券账户" type="trdAcct" v-model="trdAcct"></mt-field>
                <mt-field label="证券代码" placeholder="请输入证券代码" type="hqzqdm" v-model="hqzqdm"></mt-field>
                <mt-field  v-if=false label="证券名称:"  type="hqzqjc" v-model="hqzqjc"></mt-field>
                <mt-field label="价格" readonly type="bargainOrderPrice" v-model="bargainOrderPrice"></mt-field>
                <mt-field  v-if=false  label="可申报数量"   v-model="maxnum"></mt-field>
                <mt-field label="申报数量" placeholder="请输入申报数量" type="orderQty" v-model="orderQty"></mt-field>
                <mt-button type="danger" size="large" @click="bargainToBuy">点选买入</mt-button>
            </mt-tab-container-item>
            <mt-tab-container-item :id="tabNavs[3].selectedId">
                <mt-field label="证券账户" placeholder="请输入证券账户" type="trdAcct" v-model="trdAcct"></mt-field>
                <mt-field label="证券代码" placeholder="请输入证券代码" type="hqzqdm" v-model="hqzqdm"></mt-field>
                <mt-field  v-if=false label="证券名称:"  type="hqzqjc" v-model="hqzqjc"></mt-field>
                <mt-field label="价格" readonly type="bargainOrderPrice" v-model="bargainOrderPrice"></mt-field>
                <mt-field  v-if=false  label="可申报数量"   v-model="maxnum"></mt-field>
                <mt-field label="申报数量" placeholder="请输入申报数量" type="orderQty" v-model="orderQty"></mt-field>
                <mt-button type="danger" size="large" @click="bargainToSell">点选卖出</mt-button>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>

</template>
<script>
import cookieUtil from 'js-cookie'
import {MessageBox} from 'mint-ui'
export default{
    data(){
        return {
            conferNo:"",
            selected:"",
            bb:true,
            trdAcct:"",//证券账户
            hqzqdm:"",//证券代码
            maxnum:"",//
            orderQty:"",//
            orderPrice:'',
            bargainOrderPrice:'',//成交价格
            bargainOrderQty:'',//成交数量
            tabNavs:[{selectedId:global.STKBIZTYPE.PRICE_BUY_APPL,name:"报价买入",active:true,path:"xxxx"},
            {selectedId:global.STKBIZTYPE.PRICE_SELL_APPL,name:"报价卖出",active:false,path:"xxxx"},
            {selectedId:global.STKBIZTYPE.DEAL_BUY_APPL,name:"成交买入",active:false,path:"xxxx"},
            {selectedId:global.STKBIZTYPE.DEAL_SELL_APPL,name:"成交卖出",active:false,path:"xxxxx"},
                   // {selectedId:"cust101",name:"查询",active:false},
                ]
            }
    },
    created:function(){
        // axiosUtil.isLogin().then(message=>{
        //  var message=message.data;
        //     if(message.errorcode==0){//cookieUtil.get("userCode")){//请求接口
            this.trdAcct=cookieUtil.get("trdAcct")
            if(this.$route.query.hqzqdm){
                this.hqzqdm=this.$route.query.hqzqdm;
            }
            if(this.$route.query.conferNo){
                this.conferNo=this.$route.query.conferNo
            }
            if(this.$route.query.bargainOrderPrice){
                this.bargainOrderPrice=this.$route.query.bargainOrderPrice
            }
            if(this.$route.query.type){
                this.selected=this.$route.query.type;
                for(let i=0;i<this.tabNavs.length;i++){
                    let item=this.tabNavs[i];
                    if(this.selected===item.selectedId){
                        item.active=true;
                        var par ="";
                        if( this.hqzqdm!=""){
                            par="?hqzqdm="+this.hqzqdm
                        }
                        //this.$router.push(this.tabNavs[0].path+par)
                       // this.$router.push(this.tabNavs[i].path)
                        break;
                    }
                }
            }else{


            }

        // }else{
        //         this.$router.push("/login");
        // }
        // })

    },
    methods:{
        selectTab:function(index){
            if(index!=5 && index!=4){
                for(let i=0;i<this.tabNavs.length;i++){
                    let item=this.tabNavs[i];
                    item.active=false;
                    if(item.selectedId===this.tabNavs[index].selectedId){
                        this.tabNavs[i].active=true;
                        var par ="";
                            if( this.hqzqdm!=""){
                                par="?hqzqdm="+this.hqzqdm
                            }
                        //this.$router.push(this.tabNavs[i].path+par)
                    }
                }
                this.selected=this.tabNavs[index].selectedId;

            }


        },
        entrust:function(){
            var data={
                stkex:global.scdm,
                stkbd:global.tkbd,//报价交易板块
                trdacct:this.trdAcct,
                stkCode:this.hqzqdm,
                orderPrice:this.orderPrice,
                orderQty:this.orderQty,
                trdMode:global.trdMode.lxjj,//报价交易模式
            }
            axiosUtil.entrust(data).then(function(data){
            //    console.log(data);
            var data=data.data;
            if(data.errorcode==1){
                MessageBox("提示",data.errormsg)
            }else{
                MessageBox("提示","委托成功，委托号="+data.data)
            }
            });

        },
         sellOutopre:function(){
            var data={
                stkex:global.scdm,
                stkbd:global.tkbd,//报价交易板块
                trdacct:this.trdAcct,
                stkCode:this.hqzqdm,
                orderPrice:this.orderPrice,
                orderQty:this.orderQty,
                trdMode:global.trdMode.lxjj,//报价交易模式
            }
            axiosUtil.sellOutopre(data).then(function(data){
            //    console.log(data);
            var data=data.data;
            if(data.errorcode==1){
                MessageBox("提示",data.errormsg)
            }else{
                MessageBox("提示","委托成功，委托号="+data.data)
            }
            });

        },
        payBuy:function(){
            var data={
                stkex:global.scdm,
                stkbd:global.stkbdOffer,//报价交易板块
                trdacct:this.trdAcct,
                stkCode:this.hqzqdm,
                orderPrice:this.orderPrice,
                orderQty:this.orderQty,
                trdMode:global.trdMode.bjzr,//报价交易模式
            }
            axiosUtil.limitToBuy(data).then(function(data){
            //    console.log(data);
            var data=data.data;
            if(data.errorcode==1){
                MessageBox("提示",data.errormsg)
            }else{
                MessageBox("提示","委托成功，委托号="+data.data)
            }
            });
        },
        paySell:function(){
            var data={
                stkex:global.scdm,
                stkbd:global.stkbdOffer,//报价交易板块
                trdacct:this.trdAcct,
                stkCode:this.hqzqdm,
                orderPrice:this.orderPrice,
                orderQty:this.orderQty,
                trdMode:global.trdMode.bjzr,//报价交易模式
            }
            axiosUtil.limitToSell(data).then(function(data){
            //    console.log(data);
            var data=data.data;
            if(data.errorcode==1){
                MessageBox("提示",data.errormsg)
            }else{
                MessageBox("提示","委托成功，委托号="+data.data)
            }
            });
        },
        bargainToSell:function(){
            var data={
                stkex:global.scdm,
                stkbd:global.stkbdOffer,//报价交易板块
                trdacct:this.trdAcct,
                stkCode:this.hqzqdm,
                orderPrice:this.bargainOrderPrice,
                orderQty:this.orderQty,
                trdMode:global.trdMode.bjzr,//报价交易模式
                conferNo:this.$route.query.conferNo
                }


            axiosUtil.bargainToSell(data).then(function(data){
            //    console.log(data);
            var data=data.data;
            if(data.errorcode==1){
                MessageBox("提示",data.errormsg)
            }else{
                MessageBox("提示","委托成功，委托号="+data.data)
            }
            });

        },
        bargainToBuy:function(){
            var data={
                stkex:global.scdm,
                stkbd:global.stkbdOffer,//报价交易板块
                trdacct:this.trdAcct,
                stkCode:this.hqzqdm,
                orderPrice:this.bargainOrderPrice,
                orderQty:this.orderQty,
                trdMode:global.trdMode.bjzr,//报价交易模式
                conferNo:this.$route.query.conferNo
                }


            axiosUtil.bargainToBuy(data).then(function(data){
            //    console.log(data);
            var data=data.data;
            if(data.errorcode==1){
                MessageBox("提示",data.errormsg)
            }else{
                MessageBox("提示","委托成功，委托号="+data.data)
            }
            });

         },

    }
}

</script>
<style scoped>
.tab-nav li{
    font-size: 14px;
    color:#666;
    padding: 6px;
}
    .tab-nav .active{
        border-bottom: 2px solid #F06969;
        color :#F06969;
    }
</style>



